@import url(https://fonts.googleapis.com/css?family=Electrolize&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-radius: 5px;
}

body {
  width: 100vw;
  height: 100vh;
  font-family: Electrolize, Arial, Helvetica, sans-serif;
  /* background-color: #061f30;
  color: #00e7ff; */
}

#root {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
}
@font-face {
  font-family: 'Swiss_911_Ultra_Compressed_BT';
  /*a name to be used later*/
  src: url(/Senipah/fcc-pomodoro-clock/static/media/Swiss_911_Ultra_Compressed_BT.cb5f966d.ttf)
    /*URL to font*/
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 540px;
  height: 100%;
  font-family: Swiss_911_Ultra_Compressed_BT, Arial, Helvetica, sans-serif;
}

.app * {
  font-family: inherit;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .app {
    max-width: 720px;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .app {
    max-width: 960px;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  .app {
    max-width: 1200px;
  }
}
:root {
  --lcars-1: #FF9900;
  --lcars-2: #CC99CC;
  --lcars-3: #9999CC;
  --lcars-4: #CC6666;
  --lcars-5: #FFCC99;
  --lcars-6: #9999FF;
  --lcars-7: #FF9966;
  --lcars-8: #CC6699;
}


#clock {
  display: flex;
  flex-direction: column;
  color: #333333;
  border: 1px solid rgba(50, 50, 50, .5);
  padding: 1rem;
  align-items: center;
  justify-content: center;
}

#controls {
  display: flex;
  padding: 1rem;
}

.control {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border: 1px solid rgba(50, 50, 50, .5);
  margin: .25rem;
}


button {
  border: none;
  background-color: inherit;
  color: inherit;
  margin: .25rem;
  padding: .5rem;
  border: 1px solid rgba(50, 50, 50, .5);
  font-weight: normal;
  font-size: 1rem;
}

button::-moz-focus-inner {
  border: 0;
}

.control-label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: .5rem;
}

.control-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
}

.control-value {
  font-weight: bold;
  font-size: 1.5rem;
  color: red;
}

#timer {
  display: flex;
  flex-direction: column;
  border: 1px solid rgba(50, 50, 50, .5);
  justify-content: center;
  align-items: center;
  padding: 1rem;
}
